}
.homepageHero {
  background-color: #FDFFF7;

  height: 60vh;
  width: 100%;
  .container,.row {
    height: 100%;
  }
  h3 {
    color: #1B1B1B;
    box-shadow: rgb(252, 72, 104) -0.5rem 0px 0px;
    line-height: 1;
    padding: 0.25em;
  }
}
.productDescription {
  background-color: #6C0406;
}
.easyzoom {
	position: relative;

	/* 'Shrink-wrap' the element */
	display: inline-block;
	*display: inline;
	*zoom: 1;
}
.easyzoom {
	position: relative;

	/* 'Shrink-wrap' the element */
	display: inline-block;
	*display: inline;
	*zoom: 1;
}

.easyzoom img {
	vertical-align: bottom;
}

.easyzoom.is-loading img {
	cursor: progress;
}

.easyzoom.is-ready img {
	cursor: crosshair;
}

.easyzoom.is-error  img {
	cursor: not-allowed;
}
.easyzoom--overlay {
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.productFeatures {
  padding: 0;
  li {
    list-style: none;
    margin-bottom: 10px;
    margin-top: 10px;
  }
}
.productPrice {
  font-weight: bold;
}
.dropdown-toggle::after {
  border-top: none;
}
